'use server';

export interface Ville {
  id: number;
  nom: string;
  code_postal?: string;
  // Ajoutez d'autres propriétés selon votre modèle de données
}

export interface ApiResponse {
  success: boolean;
  data: Ville[];
  count: number;
}

/**
 * Récupère la liste de toutes les villes desservies depuis l'API FastAPI
 */
export async function getVilles(): Promise<ApiResponse> {
  try {
    const apiUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000';
    
    const response = await fetch(`${apiUrl}/villes`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      // Optionnel : cache pour optimisation
      next: { 
        tags: ['villes'],
        // revalidate: 3600 // Revalide toutes les heures
      }
    });

    if (!response.ok) {
      throw new Error(`Erreur API: ${response.status} ${response.statusText}`);
    }

    const data: ApiResponse = await response.json();
    
    return data;
  } catch (error) {
    console.error('Erreur lors de la récupération des villes:', error);
    
    // Retourne une réponse d'erreur structurée
    return {
      success: false,
      data: [],
      count: 0
    };
  }
}